'use client'

import { motion } from "framer-motion"
import Image from "next/image"
import Link from "next/link"

const projects = [
  { slug: "project-alpha", title: "Project Alpha", description: "A modern platform UI.", img: "/images/project/alpha.jpg" },
  { slug: "project-beta", title: "Project Beta", description: "Ecommerce redesign.", img: "/images/project/beta.jpg" },
  { slug: "project-gamma", title: "Project Gamma", description: "Marketing site for SaaS.", img: "/images/project/gamma.jpg" }
]

const fadeUp = {
  hidden: { opacity: 0, y: 40 },
  visible: { opacity: 1, y: 0 },
}

export default function ProjectsPage() {
  return (
    <main className="pt-28 pb-20">
      <motion.h1
        initial="hidden"
        animate="visible"
        variants={fadeUp}
        transition={{ duration: 0.6 }}
        className="text-5xl font-bold text-center mb-12"
      >
        Projects
      </motion.h1>

      <div className="container mx-auto grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-10 px-4">
        {projects.map((project, index) => (
          <motion.div
            key={project.slug}
            initial="hidden"
            animate="visible"
            variants={fadeUp}
            transition={{ duration: 0.6, delay: index * 0.15 }}
          >
            <Link href={`/projects/${project.slug}`}>
              <div className="group rounded-xl overflow-hidden shadow-md bg-white hover:shadow-xl transition-all duration-300 hover:-translate-y-2">
                
                <div className="relative h-56 w-full overflow-hidden">
                  <Image
                    src={project.img}
                    alt={project.title}
                    fill
                    className="object-cover transition-transform duration-500 group-hover:scale-110"
                  />

                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-all"></div>

                  <p className="absolute bottom-3 left-4 text-white text-lg font-semibold opacity-0 group-hover:opacity-100 transition-all">
                    {project.title}
                  </p>
                </div>

                <div className="p-4">
                  <h3 className="text-xl font-semibold text-gray-900">{project.title}</h3>
                  <p className="text-gray-600 mt-1">{project.description}</p>
                </div>

              </div>
            </Link>
          </motion.div>
        ))}
      </div>

    </main>
  )
}